  } \n\n    .entry-content > a:last-of-type {\n      display: none;\n    }\n\n  }\n\n\n  // 這是指最下面那一列\n  .post .entry-meta {\n    .buttons a {\n      cursor: pointer;\n      display: block;\n      float: left;\n      border-radius: 3px;\n      font-size: 13px;\n      padding: 7px 20px;\n      background: #333;\n      color: #fff;\n      letter-spacing: 0.5px;\n      text-transform: uppercase;\n      border: 1px solid #1D1D1D;\n      margin-top: 13px;\n      margin-bottom: 12px;\n      margin-left: 1em;\n    } \n    .buttons a:hover {\n      color: #F69087;\n    } \n\n\n    .read-more {\n      display: block;\n      float: left;\n      border-radius: 3px;\n      font-size: 13px;\n      padding: 7px 20px;\n      background: #333;\n      color: #fff;\n      letter-spacing: 0.5px;\n      text-transform: uppercase;\n      border: 1px solid #1D1D1D;\n      margin-top: 13px;\n      margin-bottom: 12px;\n    }\n  } \n\n}\n\n// ------------------------------------------\n\n@media (max-width: 1200px) {\n  #main .widget.Blog .post .index-post-body {\n    .entry-image {\n      //height: 320px;\n    }\n  }\n}\n\n\n@media (max-width: 979px) {\n  #main .widget.Blog .post .index-post-body {\n    .entry-image {\n      //height: 420px;\n    }\n  }\n}\n\n// ------------------------------------------\n\n@media (max-width: 767px) {\n  #main .widget.Blog .post {\n    .index-post-body .entry-image {\n      //height: 320px;\n    }\n    \n    .entry-meta .buttons {\n      margin-bottom: 15px;\n    }\n  }\n}\n\n/********************/\n@media (max-width: 480px) {\n  #main .widget.Blog .post .index-post-body  {\n    /*********************/\n    /* 20160926 修正小視窗底下按鈕沒有置中的問題 */\n    .entry-meta.clearfix > aside > .pull-left {\n      float: none;\n      text-align: center;\n      width: 100%;\n    }\n\n    .entry-meta.clearfix > aside > .pull-left > .read-more,\n    .entry-meta.clearfix > aside > .pull-left > span.item-control.blog-admin a {\n      float: none;\n      width: 42%;\n      display: 