foot>tr>td,\n.entry-content > article > table>tfoot>tr>th,\n.entry-content > article > table>thead>tr>td,\n.entry-content > article > table>thead>tr>th {\n  padding: 8px;\n  /*line-height: 1.42857143;*/\n  vertical-align: top;\n  border-top: 1px solid #ddd;\n}\n\n.entry-content > article > table>tbody>tr:first-of-type>td {\n  border-top-width: 0;\n}\n\n.entry-content > article > table>tbody>tr:hover {\n  background-color: #f5f5f5; \n}\n\n#main .post > h3 {\n  display: none;\n}\n\n\n\n/* ************************ */\n\n#Blog1_backlinks-container .comment-footer {\n  margin-top: 10px;\n}\n\n#footer_bar_body {\n  background: transparent;\n}\n\n/* *************************** */\n.about-me .description {\n  line-height: 1.5em;\n}\n.about-me .about-link {\n  /*float: right;*/\n  padding-bottom: 1.5em;\n}\n\n/* 20160601 加大About Me的高度 */\n#sidebar .about-me h3 {\n  margin-top: 1em;\n  margin-bottom: 0.5em;\n}\n\n#sidebar .about-me .description {\n  margin-bottom: 2em;\n}\n\n\n#sidebar .about-link .read-more {\n  float: right;\n  display: inline-block;\n  padding-left: 10px;\n  padding-right: 10px;\n  margin-left: 5px;\n  margin-top: 0;\n}\n\n#sidebar .about-link .social-button-wrapper {\n  margin-top: 4px;\n  display: inline-block;\n}\n\n.about-me .social-media {\n  margin-top: 0.5em;\n  text-align: center;\n  clear: both;\n}\n.about-me .email-subscribe {\n  text-align: center;\n  /*margin: 1em;*/\n}\n\n.about-me .widget-content {\n  margin: 0 1em;\n}\n\n.fb-fan-page .widget-content {\n  text-align: center;\n}\n\n.about-me .email-subscribe td:first-of-type {\n  text-align: left;\n  padding-top: 0.7em;\n}\n\n.about-me .email-subscribe .follow-by-email-address {\n  width: 95%;\n}\n\n.donation input[type=\"image\"] {\n  margin-top: 10px;\n  margin-right: 10px;\n}\n\n\n#footerbar-popular-post .popular-posts > ul > dd {\n  display:none; \n}\n\n#footerbar-popular-post .popular-posts > ul > dd:nth-of-type(1),\n#footerbar-popular-post .popular-posts > ul > dd:nth-of-type(2),\n#footerbar-popular-post .popular-posts > ul > dd:nth-of-type(3),\n#footerbar-popular-post .popular-posts > ul > dd:nth-of-type(4),\n#footerbar-popular-post .popular-posts > ul > dd:nth-of-type(5) {\n  display:block;\n}\n\n.popular-posts { \n  padding-bottom: 0.5em;\n}\n\n@media (max-width:979px) {\n  #footer_bar {\n    display: none;\n  } \n}\n\n.widget-content.popular-posts dd {\n  margin-bottom: 0.5em;\n  clear: both;\n}\n.widget-content.popular-posts img {\n  float: left;\n}\n\nh1.title {\n  font-size: 30px;\n}\n#header-inner .descriptionwrapper {\n  font-size: 14px;\n}\n\ndiv.post-avatar {\n  display:none;\n}\n\n.meta {\n  margin-left: 0px !important;\n}\n\n/************************/\n#content #archivespage ul {\n  margin-left: 0 !important;\n}\n\n#content #archivespage li.archivedate {\n  list-style: none !important;\n}\n\n\n.container1{\n  width:100% !important; \n}\n\n#footer .management {\n  clear: both;\n  /*margin-bottom: 70px;*/\n}\n\n@media (max-width: 979px) {\n  /*\n  .search-bar {\n      margin-top: 4px;\n      padding-left: 0;\n      padding-right: 0;\n  }\n  */\n\n  .navbar-inverse .navbar-inner {\n    background: #2d2d2d;\n  }\n  #header {\n    margin-right: 0;\n  }\n}\n\n/**********************************/\n/*\nbody .addthis-smartlayers {\n    display: none !important;\n}\n*/\n\n@media (min-width: 480px) and (max-width: 767px) {\n  .entry-container .entry-content {\n    //padding: 50px;\n    //padding-right: 20px;\n    padding: 2rem;\n  }\n}\n\n.entry-container .entry-content > p {\n  text-indent: 2em;\n}\n\n.entry-container .entry-content a.more {\n  font-weight: bold;\n  text-decoration: none;\n}\n\n/**********************************/\n/* 底部區域 */\n\n.site-footer .cloud-label-widget-content .label-size-2,\n.site-footer .cloud-label-widget-content .label-size-1 {\n  display:none;\n}\n\n\n\n.site-footer .cloud-label-widget-content .btn {\n  float: right;\n  color: black;\n  margin-top: 1em;\n}\n\n/* 20160527網頁寬度低於460時，Buy Me A Coffee跟Hit Count應該分開為左右2欄