{\n  li > a {\n    /* padding: 6px 20px 10px; */\n    line-height: 56px;\n    padding-top: 0;\n    padding-bottom: 0;\n    padding-right: 0;\n    padding-left: 10px;\n  }\n\n  li.home {\n    width: 5rem;\n\n    a.accesskey-menu {\n      padding-right: 5px;\n      padding-top: 0;\n      line-height: 54px;\n    }\n  }\n  \n  li.help .icon {\n    line-height: 29px;\n  }\n  \n  .dropdown-menu {\n    /*display: block;*/\n    position: fixed;\n    top: 54px;\n    left: calc(100vw - ((100vw - 1170px) / 2 + 10px) - 367px);\n    // 1170 / 2 = 600-15 = 585\n    li > a {\n      padding: 12px 15px;\n      line-height: 20px;\n    }\n  }\n}\n\n/* ****************************************** */\n/* @media (min-width: 1200px) { */\n\n@media (min-width: 1200px) {\n  #masthead #menu-primary {\n    .dropdown-menu {\n      margin-top: 2px;\n      margin-left: 5px;\n    }\n  }\n  \n  #masthead.fixed-nav #menu-primary {\n    .dropdown-menu {\n      margin-left: 0;\n    }\n  }\n}\n\n/* ****************************************** */\n/* @media (max-width: 1200px) { */\n\n@media (max-width: 1200px) {\n  #masthead #menu-primary {\n    li.home {\n      width: 6rem;\n    }\n    \n    .dropdown-menu {\n      /*display: block;*/\n      margin-left: -5px;\n    }\n    \n    li > a {\n      padding-left: 10px;\n      padding-right: 10px;\n    }\n    \n    li.home a.accesskey-menu {\n      padding-right: 0;\n    }\n  }\n  \n  #masthead.fixed-nav #menu-primary {\n    .dropdown-menu {\n      /*display: block;*/\n      left: calc(100vw - ((100vw - 960px) / 2 + 10px) - 277px);\n      margin-left: 0;\n    }\n  }\n}\n\n/* ****************************************** */\n/* @media (min-width: 979px) { */\n\n@media (min-width: 979px) {\n\n}\n\n// ------------------------------------\n\n\n/* ****************************************** */\n/* @media (max-width: 979px) { */\n\n@media (max-width: 979px) {\n  #masthead {\n    .nav-collapse {\n      height: 0;\n\n      #menu-primary {\n        background-color: #2D2D2D