ad #menu-primary .dropdown-menu {\n    max-height: calc(100vh - 80px);\n    overflow-x: auto;\n    width: 100%;\n    margin: 0;\n  }\n  \n  #masthead.fixed-nav #menu-primary .dropdown-menu {\n    display: none;\n  }\n}\n\n@media (max-width: 480px) and (max-height: 480px) {\n  #masthead #menu-primary .dropdown-menu {\n    max-height: calc(100vh - 106px);\n  }\n  \n}\n\n@media (max-height: 480px) and (min-width: 979px) {\n  #masthead #menu-primary .dropdown-menu {\n    width: 13rem;\n  }\n}\n"]}]);

